<template>
	<div class="bg-gradient-primary">
		<div class="container">
			<div class="row justify-content-center">
				<div class="col-xl-6 col-lg-12 col-md-9">
					<div class="card o-hidden border-0 shadow-lg my-5">
						<div class="card-body p-0">
							<div class="row">
								<div class="col-lg-12">
									<div class="p-5">
										<div class="text-center">
											<h1 class="h4 text-gray-900 mb-4">欢迎回来!</h1>
										</div>
										<form class="user" action="" @submit.prevent.stop="login">
											<div class="form-group">
												<input v-model="form.username" type="text" class="form-control form-control-user" placeholder="请输入账号">
											</div>
											<div class="form-group">
												<input v-model="form.password" type="password" class="form-control form-control-user" placeholder="请输入密码">
											</div>
											<div class="form-group">
												<div class="custom-control custom-checkbox small">
													<input type="checkbox" class="custom-control-input" id="customCheck">
													<label class="custom-control-label" for="customCheck">下次自动登录</label>
												</div>
											</div>
											<font color="red"></font>
											<button type="submit" class="btn btn-primary btn-user btn-block" id="loginsubmit">登录</button>
										</form>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import user from '../services/user.js'
	export default {
		name: 'LoginPage',
		data(){
			return{
				form:{
					username:'',
					password:''
				}
			}
		},
		mounted() {
			
		},
		methods:{
			async login(){
				try{
					const data = await user.login(this.form);
					if(data.type === 'admin'){
						localStorage.setItem("username", data.username);
						localStorage.setItem("token", 'Bearer ' + data.token);
						this.$router.replace('/');
					}else{
						this.$message.error('普通用户无法登录后台');
					}
				}catch(e){
					this.$message.error(e.error);
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import url('../assets/css/admin.min.css');
	.bg-gradient-primary{
		position: fixed;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
</style>
